.square {
    font-size: 36px;
    width: 60px;
    height: 60px;
    margin: 5px;
    border: 1px solid #999;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    cursor: pointer;
  }
  
  .square:hover {
    background-color: #f0f0f0;
  }
  
  .board {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: -150px;
  }
  
  .row {
    display: flex;
  }
  
  .game {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
  }
  
  .game-info {
    margin-left: -200px; /* Adjust this value to your preference */
    margin-top: 200px; /* Adjust this value to your preference */
    font-size: 24px;
}


button {
    background-color: #4caf50;
    color: rgb(15, 13, 13);
    border: none;
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin-top: 20px; /* Adjust this value to your preference */
    cursor: pointer;
    transition: background-color 0.3s ease;
}
